.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #7e8181;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.codepro-mai-left {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9999
}

.codepro-mai-right {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 9999
}

@media (max-width: 1024px) {
    .codepro-tet-left, .codepro-tet-right {
        display: none !important
    }
}

.codepro-tet-left {
    position: fixed;
    top: 0px;
    left: 0;
    z-index: 9999
}

.codepro-tet-right {
    position: fixed;
    top: 0px;
    right: 0;
    z-index: 9999
}

@media (max-width: 1024px) {
    .codepro-tet-left, .codepro-tet-right {
        display: none !important
    }
}